import React, { useState } from 'react'
import { Link, useParams } from 'react-router-dom'

import Tabs from './global/Tabs'
import Controls from './global/Controls'
import BackArrowButton from './global/buttons/BackArrowButton'
import NextArrowButton from './global/buttons/NextArrowButton'
import { Turquoise } from './global/colors'
import InlineIconLabel from './global/InlineIconLabel'
import { candidatesDb } from './global/mock'
import Avatar from './global/Avatar'

import { Context } from "./App"

const sortTechStack = tech_stack => {
  function compare(a, b) {
    if (a.name < b.name) {
      return -1;
    }
    if (a.name > b.name) {
      return 1;
    }
    return 0;
  }
  let allTech = {
    0: [],
    1: [],
    2: [],
    3: [],
  }
  tech_stack.map(tech => {
    allTech[tech.experience].push(tech)
  })

  allTech[0].sort(compare)
  allTech[1].sort(compare)
  allTech[2].sort(compare)
  allTech[3].sort(compare)

  return [...allTech[3], ...allTech[2], ...allTech[1], ...allTech[0]]
}

function JobApplicants(params) {
  const { state, dispatch } = React.useContext(Context)

  const { id, page } = useParams()
  let defaultTab = 0
  if (!page) {
    defaultTab = 0
    params.history.push({
      pathname: '/job/' + id + '/candidates/new-matches',
    })
  }
  if (page === 'in-process') {
    defaultTab = 1
  }
  if (page === 'candidates-accepted') {
    defaultTab = 2
  }
  const [index, setIndex] = useState(defaultTab)

  let data = state.recruiter.activeJobs[id]
  if (!data) {
    return (
      <div>Make an error page and tell the user they can't see this job's candidates</div>
    )
  }
  Object.keys(data.new_matches).map(candidateId => {
    data.new_matches[candidateId].tech_stack = sortTechStack(data.new_matches[candidateId].tech_stack)
  })
  Object.keys(data.in_process).map(candidateId => {
    data.in_process[candidateId].tech_stack = sortTechStack(data.in_process[candidateId].tech_stack)
  })
  Object.keys(data.accepted_candidates).map(candidateId => {
    data.accepted_candidates[candidateId].tech_stack = sortTechStack(data.accepted_candidates[candidateId].tech_stack)
  })

  const setTab = index => {
    if (index === 0) {
      params.history.push({
        pathname: '/job/' + id + '/candidates/new-matches',
      })
    }
    if (index === 1) {
      params.history.push({
        pathname: '/job/' + id + '/candidates/in-process',
      })
    }
    if (index === 2) {
      params.history.push({
        pathname: '/job/' + id + '/candidates/candidates-accepted',
      })
    }
    setIndex(index)
  }

  return (
    <div>
      <Controls
        left={<Link to='/jobs-overview'><BackArrowButton label='Jobs Overview' /></Link>}
        right={<NextArrowButton label='Java Developer' />}
      />
      <h1>Frontend Developer</h1>
      <div className='columns' style={{ marginTop: 12 }}>
        <div className='column is-8'>
          <Tabs
            tabs={[
              'New Matches (' + Object.keys(data.new_matches).length + ')',
              'In Process (' + Object.keys(data.in_process).length + ')',
              'Candidates Accepted (' + Object.keys(data.accepted_candidates).length + ')'
            ]}
            position={index}
            setPosition={setTab}
          />
        </div>
      </div>


      {index === 0 && data.new_matches && Object.keys(data.new_matches).map(candidateId => (
        <Applicant applicant={data.new_matches[candidateId]} id={id} page={page}>
          <Controls
            right={[
              <InlineIconLabel icon='fas fa-thumbs-up' color='#D1D1D6' onClick={() => dispatch({ type: 'QUICK_MATCH_CANDIDATE', jobId: id, candidateId: candidateId })}>
                Quick Match
              </InlineIconLabel>
            ]}
          />
        </Applicant>
      ))
      }

      {
        index === 1 && data.in_process && Object.keys(data.in_process).map(candidateId => (
          <Applicant applicant={data.in_process[candidateId]} id={id} page={page}>
            <div style={{ position: 'relative', height: '100%' }}>
              <Controls
                right={[
                  <InlineIconLabel
                    icon='fas fa-file-alt'
                    color='#D1D1D6' onClick={() => dispatch({ type: 'REQUEST_CV', jobId: id, candidateId: candidateId })}>
                    {data.in_process[candidateId].cv_requested && <span>CV Requested</span>}
                    {!data.in_process[candidateId].cv_requested && <span>Request CV</span>}
                  </InlineIconLabel>,
                  <InlineIconLabel icon='fas fa-comments' color='#D1D1D6'>
                    Show Conversation
                  </InlineIconLabel>
                ]}
              />
              <div style={{ position: 'absolute', bottom: 0, textAlign: 'right', width: '100%' }}>
                <InlineIconLabel icon='fas fa-times' color='#D1D1D6' onClick={() => dispatch({ type: 'CANCEL_PROCESS_FOR_CANDIDATE', jobId: id, candidateId: candidateId })}>
                  Cancel Process
                </InlineIconLabel>
              </div>
            </div>
          </Applicant>
        ))
      }

      {
        index === 2 && data.accepted_candidates && Object.keys(data.accepted_candidates).map(candidateId => (
          <Applicant applicant={data.accepted_candidates[candidateId]} id={id} page={page}>
            <div style={{ position: 'relative', height: '100%' }}>
              <Controls
                right={[
                  <InlineIconLabel icon='fas fa-download' color='#D1D1D6'>
                    Download CV
                </InlineIconLabel>,
                  <InlineIconLabel icon='fas fa-comments' color='#D1D1D6'>
                    Show Conversation
                  </InlineIconLabel>
                ]}
              />
            </div>
          </Applicant>
        ))
      }

    </div >
  )
}

export default JobApplicants

function Applicant(params) {

  const [showMore, setShowMore] = useState(false)

  return (
    <div style={{ borderBottom: '1px solid #E4E4E4', marginTop: 24 }}>
      <div className='columns is-gapless is-marginless'>
        <div className='column is-1'>
          <Link to={'/job/' + params.id + '/candidates/' + params.page + '/' + params.applicant.id}>
            <Avatar size={64} src={params.applicant.profile_picture} />
          </Link>
        </div>
        <div className='column is-8' style={{ marginBottom: 12 }}>
          <div>
            <div>
              <Link to={'/job/' + params.id + '/candidates/' + params.page + '/' + params.applicant.id}>
                <h3 style={{ display: 'inline', marginRight: 12 }}>{params.applicant.name}</h3>
              </Link>
              <InlineIconLabel icon='fas fa-map-marker' color='#D1D1D6'>
                {params.applicant.location}
              </InlineIconLabel>
            </div>
            <div>
              {params.applicant.tech_stack && params.applicant.tech_stack.slice(0, 3).map(tech => (
                <div style={{
                  backgroundColor: `${tech.experience === 3 ? '#7F4CDC' : ''}${tech.experience === 2 ? '#A079E5' : ''}${tech.experience === 1 ? '#CCB8F1' : ''}`,
                  display: 'inline-block',
                  color: '#FFFFFF',
                  fontWeight: 'bold',
                  margin: 4,
                  paddingTop: 4,
                  paddingBottom: 4,
                  paddingLeft: 12,
                  paddingRight: 12,
                }}>{tech.name}</div>
              ))}
            </div>
            <div>
              {!showMore && params.applicant.work && params.applicant.work.slice(0, 2).map(work => (
                <div>{work.role} - {work.company.name} | {work.start_date} - {work.end_date}</div>
              ))}

              {!showMore && params.applicant.work && params.applicant.work.length > 2 && <div
                style={{ fontWeight: 'bold', cursor: 'pointer', color: Turquoise }}
                onClick={event => setShowMore(!showMore)}
              >
                Show {params.applicant.work.length - 2} More Experiences <i className='fas fa-chevron-down' />
              </div>}

              {showMore && params.applicant.work && params.applicant.work.map(work => (
                <div>{work.role} - {work.company.name} | {work.start_date} - {work.end_date}</div>
              ))}

              {showMore && params.applicant.work && params.applicant.work.length > 2 && <div
                style={{ fontWeight: 'bold', cursor: 'pointer', color: Turquoise }}
                onClick={event => setShowMore(!showMore)}
              >
                Show Less Experiences <i className='fas fa-chevron-up' />
              </div>}
            </div>
          </div>
        </div>
        <div className='column is-3' style={{ textAlign: 'right', marginTop: 16 }}>
          {params.children}
        </div>
      </div>
    </div >
  )
}